import styles from "../styles/ProgressIndicator.module.css";

interface Props {
  value: number;
}

export const ProgressIndicator: React.FC<Props> = (props: Props) => {
    return (
        <div className={styles.outter}>
            <div className={styles.inner} style={{width: `${Math.min(props.value, 100)}%`}}/>
        </div>
    )
}